import React, { useContext } from 'react'

import { WarehouseContext } from '../../context/warehouse/warehouse.context'

import {
    ModalContainer
} from './modal-info-container.styles'

const ModalInfoContaier = ({ product }) => {
    console.log(product)
    const { products } = useContext(WarehouseContext)
    console.log(products)

    const selectedProduct = products.find(prdtc => prdtc._id === product.product)

    console.log(selectedProduct)


    return (
        <ModalContainer>
            <h1>{selectedProduct.name}</h1>
            <p>{selectedProduct.eanCode}</p>
            <p>{(selectedProduct.price / 100).toFixed(2)}€</p>
        </ModalContainer>
    )
}

export default ModalInfoContaier
